.load-more.is-hidden {
  display: none;
}

body {
  margin: unset;
}

img {
  display: block;
  width: 100%;
  max-height: 100%;
  padding-bottom: 5px;
}
.form-wrapper {
  position: sticky;
  top: 0;
  width: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #5060ba;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
    0px 2px 1px rgb(0 0 0 / 20%);
}

.search-button {
  border-radius: 0px 4px 4px 0px;
  border: none;
  padding-left: 10px;
  padding-right: 10px;
}

.input {
  border-width: 2px;
  border-radius: 4px 0px 0px 4px;
  width: 25%;
  border: 1px solid rgba(255, 255, 255, 0.3);
  outline: none;
  background: white;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 8px;
}

.input-wrapper {
  display: flex;
  justify-content: center;
  /* border-radius: 4px; */
  /* border: 1px solid rgba(255, 255, 255, 0.3); */

  /* border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.3); */
}

.search-form {
  /* border-radius: 4px; */
  /* border: 1px solid rgba(255, 255, 255, 0.3); */
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 15px;
  margin-top: 30px;
  margin-bottom: 30px;
  /* display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: -10px;
  margin-right: -10px; */
}

.photo-card {
  display: inherit;
  width: 100%;
  align-items: center;
  object-fit: cover;
  border-radius: 0px 0px 4px 4px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
    0px 2px 1px rgb(0 0 0 / 20%);
}

.photo-card:hover {
  transform: scale(1.055);
}
/* .photo-card:hover{
